import React from 'react';

const Scrollamelo = (props) => {


	if(window.Scrollamelo){


	    window.Scrollamelo.addEventListener("scroll", function(e){

	    	const altezzaScroll = window.Scrollamelo.scrollHeight-window.Scrollamelo.offsetHeight;

	        let per = Math.round((e.target.scrollTop/altezzaScroll)*100);

	        window.progressScrollingfiglio.style.width = `${per}%`;

					//console.log(props)
					/*cosa c'è nel viewport?
					if(props.type!=="desk"){
						let immagini = document.getElementsByClassName("card");
				    for(var counter=0; counter<immagini.length;counter++){
				      if(/\d/.test(immagini.item(counter).id)){

								if(


								(!immagini.item(counter).classList.contains("displayNone"))

								)

								if
								((immagini.item(counter).getBoundingClientRect().top<window.screen.availHeight+500)
								&&
								(immagini.item(counter).getBoundingClientRect().top>-immagini.item(counter).height)
								)
								{
									//in viewport
									immagini.item(counter).classList.add("cardDibCallUp")
									immagini.item(counter).classList.remove("cardDibCallDown")
								}else{
										//console.log(immagini.item(counter).height)
										immagini.item(counter).classList.add("cardDibCallDown")
										immagini.item(counter).classList.remove("cardDibCallUp")
									//out viewport
								}




							//console.log(window.innerHeight)

			      	}
			    	}
					}
					/**/


	    })
	}

	return (


			<div id="progressScrollingpadre" className="progressScrollingpadre" >
				<div id="progressScrollingfiglio" className="bg-white-30 progressScrollingfiglio"></div>
			</div>

		);





};

export default Scrollamelo;



